
import Qrcode from 'vux/src/components/qrcode/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import follow from '../../components/wechat/follow'
import nearShop from '../../components/goods/nearShop'
export default {
directives: {
TransferDom
},
components: {
Qrcode,
XDialog,
TransferDom,
XButton,
follow,
Flexbox,
FlexboxItem,
nearShop,
Cell,
Group
},
name: 'giveVcard',
data () {
return {
'card_code': '',
t: true,
message: '领取洗车卡需要先关注公众号哦。',
subscribe: false,
givevcard: {
Customer: {
cname: ''
},
toCustomer: {},
status: 2
},
error: 0,
nearShop: {},
nearShopList: []
}
},
created: function() {

},
activated: function(){
this.card_code = this.$route.params.card_code
console.log(this.card_code)
this.fetchData()
this.getGiveVcard()
this.getNearShop()
},
deactivated: function(){

},
computed: {
getBtnMsg: function () {
var btnMsg = '超时24小时已退回'
var self = this
if(self.error == 0) btnMsg = '点击领取'

if(self.givevcard.status == 1 && self.givevcard.toCustomer) btnMsg = '已被'+self.givevcard.toCustomer.cname+'领取'
if(self.error == 20000) btnMsg = '领取成功'  
return btnMsg
}
},
methods: {
fetchData: function(){
let self = this
self.xarpost('Wechat/ckechfollow').then( d => {
let {data, error, message} = d
console.log(data)
if(error === 0) self.subscribe = data.subscribe
})
},
getGiveVcard: function (){
let self = this
self.xarpost('Vcard/getGiveVcardInfo', {card_code: self.card_code}).then( d => {
let {data, error, message} = d
self.error = error
self.givevcard = data
if(error === 0){
var cname = data.Customer && data.Customer.cname ? data.Customer.cname : '您的好友';
var title = cname + '赠送了您一张洗车卡，快来领取吧';
self.setWechatTitle(title)
if(data.status == 1){
self.noticeBuy()
}
}else{
self.noticeBuy(message+'，点击确定、带您购买新的洗车卡')
}
})
},
lingqu: function(){
let self = this
if(self.error != 0) return
if(!self.subscribe){
this.$refs.follow.showBox = true
}else{
self.xarpost('Vcard/getGiveVcard', {card_code: self.card_code}).then( d => {
let {data, error, message} = d
if(error === 0){
self.error = 20000
self.showtoast('领取成功')
}else{
self.showtoast(message)
}
});
}
},
noticeBuy: function (content = '该洗车卡已被领取，现在带您去购买新的洗车卡？'){
let self = this
self.$vux.alert.show({
title: '小矮人',
content: content,
onHide () {
self.$router.push('/vcard/2')
}
})
},
getNearShop: function(){
var self = this
self.getLocation(false).then(function(res){
self.xarpost('Shopinfo/getNearShop', {lat: res.latitude, lng: res.longitude}).then((d) => {
let {data, error, message} = d
self.nearShop = data[0]
self.nearShopList = data
})
})
},
}
}



